.js-sortable-handle {
	cursor: move; }

[data-popup-wrapper="products-filters"] {
	&.is-open {
		overflow: hidden; } }

.splitter {
	position: absolute;
	@include nosel;
	&_right,
	&_left {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 15px;
		background: $lightGray;
		cursor: e-resize;
		@include touch {
			width: 40px; }
		.splitter {
			&__lines {
				border-left: 1px solid rgba($black, 0.4);
				border-right: 1px solid rgba($black, 0.4);
				width: 4px;
				height: 10px; }
			&__arrow {
				color: rgba($black, 0.4);
				font-size: 36px; } } }
	&_right {
		right: 0; }
	&_left {
		left: 0; }
	&__lines,
	&__arrow {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate3d(-50%,-50%,0); }
	&__lines {
		@include touch {
			display: none; } }
	&__arrow {
		display: none;
		transition: transform 0.3s ease-in-out;
		@include touch {
			display: block; }
		.splitter.is-active & {
			transform: rotate(180deg) translate3d(50%,50%,0); } } }


hr {
	clear: both;
	margin: 0;
	border: none;
	width: 100%;
	height: 2px;
	color: rgba($black, 0.1);
	background: rgba($black, 0.1); }
