.graph {
	// + .graph
	// 	margin-top: 20px
	&__body {
		height: 315px;
		@include clr; }
	&__frame {
		position: relative;
		float: left;
		width: 50%;
		height: 100%; }
	&__canvas-container {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 200px;
		height: 200px;
		transform: translate3d(-50%,-50%,0); }
	&__subtitle {
		position: absolute;
		top: 50%;
		left: 50%;
		padding: 5px;
		width: 140px;
		text-align: center;
		transform: translate3d(-50%,-50%,0);
		strong,
		span {
			display: block;
			line-height: 1; }
		strong {
			padding-bottom: 10px;
			font-size: 18px; }
		span {
			font-size: 16px; } }
	&__info {
		position: relative;
		overflow: hidden;
		height: 100%;
		// border-top: 1px solid $lightGray
		// border-left: 1px solid $lightGray
		// height: 380px
 }		// height: 165px
	&__table {
		position: relative;
		padding-top: 41px;
		height: 100%;
		&-head {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			border-bottom: 1px solid $lightGray;
			background: #fcfcfc; }
		&-head,
		&-body-in {
			display: table;
			table-layout: fixed;
			width: 100%; }
		&-body {
			overflow: auto;
			height: 100%; } }
	&__tr {
		display: table-row; }
	&__th {
		@include rmed;
		font-size: 16px; }
	&__td {
		border-bottom: 1px solid $lightGray;
		font-size: 14px; }
	&__td,
	&__th {
		display: table-cell;
		vertical-align: middle;
		padding: 10px;
		line-height: 20px;
		&[data-column*="color"] {
			width: 60px; }
		&[data-column*="category-qty"],
		&[data-column*="category-price"],
		&[data-column*="category-percent"] {
			text-align: right; }
		&:first-child {
			padding-left: 30px; }
		&:last-child {
			padding-right: 30px; } }
	&__color {
		display: block;
		width: 20px;
		height: 20px; } }

.graph-list {
	overflow: auto;
	height: 100%;
	padding: 5px 0;
	&__item {
		position: relative;
		padding: 3px 20px 3px 40px;
		@include rreg;
		font-size: 14px;
		line-height: 16px; }
	&__color {
		position: absolute;
		top: 4px;
		left: 0;
		width: 30px;
		height: 30px; }
	&__title {
		@include rmed; } }


