html,
body,
.out,
.main,
.vertical-center,
.fullscreen {
	height: 100%; }

.out {
	position: relative; }

.main {
	position: relative;
	&_inner {
		padding-top: 50px; }
	&_lg {
		padding-top: 150px;
		@include hsm {
			padding-top: 100px; } }
	&_md {
		padding-top: 90px; } }

.vertical-center {
	@include vertical;
	&__container {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		font-size: 16px;
		text-align: left; } }

.section {
	padding: 20px; }

.inner {
	margin: 0 auto;
	max-width: 720px;
	@include clr; }

.l-container,
.l-row,
.l-col {
	height: 100%; }
.l-container {
	position: relative;
	z-index: 101; }
.l-row {
	position: relative;
	@include clr; }
.l-col {
	position: absolute;
	top: 0;
	bottom: 0;
	&.is-animation {
		transition: width 0.4s ease; }
	&_sidebar {
		z-index: 5;
		left: 0;
		width: 41%;
		max-width: 50%;
		min-width: 400px;
		.l-col__inner {
			padding-top: 50px; } }
	&_main {
		z-index: 10;
		right: 0;
		width: 59%;
		max-width: 100%;
		min-width: 50%;
		.l-col__inner {
			padding-left: 15px;
			@include touch {
				padding-left: 40px; } } }
	&__inner {
		position: relative;
		height: 100%; }
	.l-col__splitter {
		top: 50px; } }
