.modal-content {
	position: relative;
	border-radius: 5px;
	background: $white;
	box-shadow: 0 0 5px 1px rgba($black, 0.4);
	&_fullscreen {
		padding-top: 48px;
		height: 100%;
		.modal-content {
			&__header {
				position: absolute;
				top: 0;
				left: 0;
				right: 0; } } }
	&__header {
		position: relative;
		padding: 11px 20px;
		border-radius: 5px 5px 0 0;
		min-height: 50px;
		color: $white;
		background: $mainColor; }
	&__body {
		padding: 20px;
		&[data-scrollbar] {
			overflow: auto;
			height: 100%; } }
	&__footer {
		padding: 20px;
		border-top: 1px solid $mainColor;
		@include clr; }
	&__success {
		float: right;
		min-width: 100px; }
	&__title {
		@include text-overflow; }
	&__close {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 50px;
		color: inherit;
		background: none;
		font-size: 30px;
		@include active {
			color: inherit; } }
	&__help-close {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; } }

.help-close {
	display: block;
	padding: 0 20px;
	width: 100%;
	height: 50px;
	color: $white;
	background: none;
	font-size: 0;
	text-align: left;
	@include active {
		color: $white; }
	&__icon,
	&__arrow {
		display: inline-block;
		vertical-align: top;
		line-height: 50px; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		font-size: 24px; }
	&__arrow {
		font-size: 20px; } }
