button,
.btn {
	-webkit-tap-highlight-color: transparent;
	&:disabled,
	&.is-disabled {
		opacity: 0.5;
		pointer-events: none; } }

.btn {
	display: inline-block;
	vertical-align: top;

	padding: 0 10px;
	margin: 0;
	border: none;
	min-width: 40px;
	height: 40px;

	color: $darkGray;
	background: $lightGray;

	@include rlight;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-decoration: none;

	white-space: nowrap;

	transition: background 0.175s ease, box-shadow 0.175s ease;

	cursor: pointer;
	user-select: none;
	@include active {
		color: $darkGray;
		background: $lightGrayActive;
		box-shadow: inset 2px 2px 6px rgba($black, 0.2); }
	.icon {
		vertical-align: top; }
	.fa {
		margin-right: 10px; }

	//mods
	&_lg {
		padding: 0 15px;
		min-width: 60px;
		height: 60px;
		font-size: 19px;
		line-height: 60px;
		.icon {
			margin-top: 10px; }
		.icon-more-v {
			margin-top: 0; } }

	&_block {
		display: block;
		width: 100%; }

	&_green {
		color: $white;
		background: $green;
		@include active {
			color: $white;
			background: $greenActive; } }
	&_red {
		color: $white;
		background: $red;
		@include active {
			color: $white;
			background: $redActive; } }
	&_main {
		color: $white;
		background: $mainColor;
		@include active {
			color: $white;
			background: $mainColorActive; } }
	&_blue {
		color: $white;
		background: $blue;
		@include active {
			color: $white;
			background: $blueActive; } }
	&_azure {
		color: $blue;
		background: $azure;
		@include active {
			color: $blue;
			background: $azureActive; } }
	&_white {
		color: inherit;
		background: $white;
		@include active {
			background-color: $eb; } }
	&_transparent {
		color: $blue;
		background: transparent;
		@include active {
			color: $blue;
			background: transparent; } }
	&_border {
		border: 1px solid $lightGray; }

	&_more {
		.fa {
			padding: 0 10px;
			margin: 0;
			font-size: 20px; } } }

.btn-action {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	height: 45px;
	color: $blue;
	font-size: 24px;
	line-height: 45px;
	text-align: center;
	i {
		font-size: inherit !important; } }

.btn-back {
	height: 50px;
	color: inherit;
	background: none;
	@include rreg;
	font-size: 0;
	@include active {
		color: inherit; }
	&__arrow,
	&__content {
		display: inline-block;
		vertical-align: top;
		line-height: 50px; }
	&__arrow {
		padding-right: 10px;
		font-size: 32px; }
	&__content {
		font-size: 20px; } }

.btn-close {
	display: inline-block;
	vertical-align {}
	width: 60px;
	height: 60px;
	background: none;
	font-size: 28px;
	line-height: 60px;
	// color: lighten($red, 4%)
	color: #E57373; }

.btn-toggle {
	display: inline-block;
	vertical-align: top;
	overflow: hidden;
	padding: 0 15px;
	height: 60px;
	background: none;
	@include rreg;
	font-size: 0;
	&.is-active {
		.btn-toggle {
			&__track {
				transform: translate3d(0,-50%,0); }
			&__arrow {
				transform: rotate(-180deg); } } }
	&__track,
	&__arrow {
		display: inline-block;
		vertical-align: top;
		transition: transform 0.175s ease-in-out; }
	&__content {
		display: block;
		font-size: 18px;
		line-height: 60px; }
	&__arrow {
		margin-left: 10px;
		font-size: 24px;
		line-height: 60px; } }
