.textarea-box {
	position: relative;
	&.is-error {
		textarea {
			border-color: $red; } }
	&.is-active {
		.textarea-box__placeholder {
			transform: translate3d(0,10px,0) scale(0.73); } }
	@include hover {
		.textarea-box__clear {
			opacity: 1;
			visibility: visible; } }
	textarea:focus + .textarea-box__clear {
		opacity: 1;
		visibility: visible; }
	&__clear {
		position: absolute;
		top: 50%;
		right: 15px;

		width: 40px;
		height: 40px;
		border-radius: 50%;

		opacity: 0;
		visibility: hidden;
		color: $darkGray;
		background: $lightGray;

		transform: translate3d(0,-50%,0);
		transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, color 0.175s ease-in-out, background 0.175s ease-in-out;
		@include active {
			color: $darkGray;
			background: $lightGrayActive; }
		i {
			font-size: 18px; } }
	&__field {
		position: relative;
		display: block;
		cursor: text; }
	&__textarea {
		padding-top: 26px;
		padding-right: 60px; }
	&__error-message {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		@include text-overflow; }
	&__placeholder {
		position: absolute;
		top: 0;
		left: 15px;
		right: 15px;
		color: rgba($darkGray, 0.7);
		@include rreg;
		font-size: 18px;
		line-height: 20px;
		transform: translate3d(0,20px,0);
		transform-origin: top left;
		transition: transform 0.15s ease-in-out;
		@include nosel;
		pointer-events: none; } }
