.m-item {
	&:not(.m-item_single) {
		background: #fcfcfc;
		@include hover {
			.m-item {
				&__container {
					background: rgba($mainColor, 0.03); } } } }
	&__container {
		padding: 20px 10px;
		@include lg {
			padding-bottom: 5px; } }
	&__header {
		position: relative;
		@include clr; }
	&__field,
	&__toggle,
	&__close {
		float: left;
		padding: 0 10px; }
	&__field {
		position: relative;
		width: 300px;
		@include lg {
			padding-right: 60px;
			width: 100%; } }
	&__icon {
		position: absolute;
		z-index: 10;
		top: 0;
		left: 10px;
		bottom: 0;
		color: darken($lightGray, 10%);
		font-size: 24px;
		line-height: 60px;
		cursor: move;
		~ .m-item__field {
			padding-left: 50px; } }
	&__toggle {
		width: 200px;
		@include lg {
			width: 100%;
			text-align: center; } }
	&__close {
		width: 80px;
		@include lg {
			position: absolute;
			top: 0;
			right: -8px;
			padding: 0;
			width: 60px; } }
	&__btn-toggle {
		width: 100%;
		@include lg {
			width: auto; } }
	&__body {
		&.js-toggle-container {
			display: none;
			&.is-open {
				display: block; } } }
	&__body {
		padding: 10px; }
	&__fake-buttons {
		max-width: 600px; }
	&__items {
		clear: both;
		padding: 20px 0;
		margin: 0 -20px; }
	&__label {
		padding-left: 70px;
		padding-bottom: 8px;
		font-size: 18px;
		text-align: left; }
	&__add {
		padding: 15px 60px 0; }
	&__checkbox {
		padding: 5px 40px; } }
