.products {
	&.is-open {
		z-index: 105;
		.products-overlay {
			opacity: 1;
			visibility: visible; } } }

.products-sidebar {
	position: relative;
	padding-top: 140px;
	height: 100%;
	background: $alabaster;
	&_simple {
		padding-top: 80px;
		@include md {
			padding-top: 41px; } }
	&__header {
		position: absolute;
		top: 0;
		left: 0;
		right: 0; }
	&__search {
		padding: 20px;
		@include md {
			padding: 1px 0 0; } } }

.products-main {
	position: relative;
	overflow: hidden;
	padding-top: 50px;
	height: 100%;
	background: $alabaster;
	&__header {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 50px; }
	&__title {
		padding-top: 13px;
		padding-bottom: 13px;
		margin: 0;
		font-size: 20px;
		line-height: 1.2;
		text-align: center; } }

.products-overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 100;
	opacity: 0;
	visibility: hidden;
	background: rgba(#434c55, 0.8);
	transition: opacity 0.175s ease-in-out, visibility 0.175s ease-in-out; }

