.splitter {
  position: absolute;
  user-select: none;
}

.splitter_right, .splitter_left {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 15px;
  background: #eee;
  cursor: e-resize;
}

.is-touch .splitter_right, .is-touch .splitter_left {
  width: 40px;
}

.splitter_right .splitter__lines, .splitter_left .splitter__lines {
  border-left: 1px solid rgba(0, 0, 0, 0.4);
  border-right: 1px solid rgba(0, 0, 0, 0.4);
  width: 4px;
  height: 10px;
}

.splitter_right .splitter__arrow, .splitter_left .splitter__arrow {
  color: rgba(0, 0, 0, 0.4);
  font-size: 36px;
}

.splitter_right {
  right: 0;
}

.splitter_left {
  left: 0;
}

.splitter__lines, .splitter__arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.is-touch .splitter__lines {
  display: none;
}

.splitter__arrow {
  display: none;
  transition: transform 0.3s ease-in-out;
}

.is-touch .splitter__arrow {
  display: block;
}

.splitter.is-active .splitter__arrow {
  transform: rotate(180deg) translate3d(50%, 50%, 0);
}

